import React, { Component, PropTypes } from 'react'
import CodeMirror from 'react-codemirror';

export default class TemplateEditor extends Component {
    constructor(props) {
        super(props);
        this.state = this.getInitialState();
        fetch('/api/templates/' + localStorage.getItem("templateName"),
            {
                method: 'POST',
                credentials: "same-origin",
                headers: {
					'Content-Type': 'application/json'
				}
            })
            .then(result => result.body.getReader().read())
            .then(res => new TextDecoder().decode(res.value))
            .then(templateName => {
                debugger;
                localStorage.setItem("templateName", templateName);
                window.location.replace('templateEditor.html');
            });
    }

    getInitialState() {
        return {
            code: "// Code",
        };
    }

    updateCode(newCode) {
        this.setState({
            code: newCode,
        });
    }

    render() {
        var options = {
            lineNumbers: true,
            mode: "htmlmixed"
        };
        var style = {
            height: '100vh'
        }
        return <CodeMirror style={style} value={this.state.code} onChange={this.updateCode.bind(this)} 
            options={options}/>
    	}
}


